:root {
    --mainBlue: #2a2a72;
    --lightBlue: #009ffd;
    --mainWhite:  #f3f3f3;
    --mainDark: #232528;
    --mainYellow: #ffa400;
}


body {
    font-family: 'Oswald', sans-serif !important;
    background: var(--mainWhite) !important;
    color: var(--mainDark);
}


.text-title {
    font-family: 'Permanent Marker', cursive;
    letter-spacing: .3rem;
    text-transform: uppercase;
}


.text-blue {
    color: var(--mainBlue);
}


.text-bright {
    color: var(--lightBlue);
}



.btn-black {
    background: transparent;
    text-transform: capitalize;
    font-size: .8rem !important;
    font-weight: 700;
    color: var(--mainDark);
    border-radius: 0 !important;
    border: .2rem solid var(--mainDark) !important;
}

.btn-black:hover{
    background: var(--mainDark) !important;
    color: var(--mainWhite) !important;
}


.cart-icon{
    cursor: pointer;
    font-size: 1.2rem;
    color: var(--mainYellow);
}